.criar-divida {
  width: 100%;
  height: 100%;
  overflow: hidden;
}


.criar-divida-titulo {
    position: relative;
    height: 5vh;
    top: 4vh;
    font-family: fonteTitulo;
    font-weight: bold;
    font-size: 2vh;
    text-align: center;
    letter-spacing: 0.01em;
    color: #3E3C39;
}


.descricao-label{
  position: relative;
  font-family: fonteLabel;
  font-weight: 550;
  font-size: 2.1vh;
  display: flex;
  color: #3E3C39;

  left: 16%;
  height: 3.5vh;
}

.input-descricao {
    position: relative;
    width: 67%;
    height: 25vh;
    left: 12.5%;
    padding-left: 4vw;
    padding-top: 2vh;
    font-size: 2vh;
    letter-spacing: 0.01em;
    resize: none;
    background: #FFFFFF;
    white-space: pre-wrap;
    outline: none;

    box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.08);
    border-radius: 30px;
    border: #ffffff;
    
    color: #AEADAA;
}

.button-criar-divida {
  margin-bottom: 4vh;
}


/* remove spin wheel input numbers */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  -moz-appearance: textfield;
}

/* posições */
.credor-css {
    margin-top: 7vh;
    height: 11.5vh;
}

.devedor-css {
  height: 11.5vh;
}

.quantia-css {
  height: 11.5vh;
}

.descricao-css {
  height: 28vh;
}

.sidebar-mobile {
  display: none;
}


@media (min-width: 961px){

  .sidebar-mobile {
    display: block;
    width: 20%;
  }

  .topbar-mobile {
    display: none;
  }

  .criar-divida-titulo {
    margin-left: 17vw;
    font-size: 2.5vh;
  }

  .labelInput {
    width: 100%;
  }
  .labelInput label {
    height: 3.5vh;

  }

  .inputs{
    display: flex;
    margin-bottom: 1vh;
  }

  .labelInput input {
    height: 6vh;
    width: 27vw;
    padding-left: 1.5vw;
  }
  
  .credor-css {
    margin-top: 18vh;
    margin-left: 22.5vw;
    height: 14vh;
    width: 100%;
  }

  .devedor-css {
    margin-left: 22.5vw;
    height: 14vh;
    width: 100%;
  }

  .quantia-css {
    margin-left: 22.5vw;
    height: 14vh;
    width: 100%;
  }

  .descricao-css {
    margin-top: 18vh;
    margin-left: 7vw;
    width: 27vw;
  }

  .descricao-label{
    height: 3.5vh;
  }

  .input-descricao {
      width: 100%;
      height: 32vh;
      padding-left: 1vw;
      padding-top: 2vh;
  }

  .button-criar-divida {
    width: 27%;
    margin-left: 22vw;
  }

}

.popup .popupzito {
  visibility: hidden;
  z-index: 1;
  position: absolute;
  width: 83%;
  height: 31%;
  left: calc(50% - 83% / 2);
  top: 36%;

  background: #ffffff;
  box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.08);
  border-radius: 30px;
}

.popuptext {
  visibility: hidden;
  color: #f08a6e;
  z-index: 2;
  position: absolute;
  font-family: fonteLabel;
  font-size: 7vw;
  line-height: 3.2%;
  width: 83%;
  height: 5.3%;
  top: 41.4%;
  margin-left: 11.2%;
}

.circle {
  visibility: hidden;
  position: absolute;
  z-index: 2;
  width: 22.7%;
  height: 10.5%;
  left: 40%;
  top: 50.5%;
  border-radius: 50%;

  background-color: #F08A6E;
  cursor: pointer;

}

.check {
  visibility: hidden;
  position: absolute;
  z-index: 3;
  width: 4.15%;
  height: 4.2%;
  transform: rotate(45deg);
  border-bottom: 7px solid #ffffff;
  border-right: 7px solid #ffffff;
  left: 48.5%;
  bottom: 42%;

  cursor: pointer;
}

/* hide and show the popup */
.popup .show {
  visibility: visible;
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
}

.popuptext .show {
  visibility: visible;
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
}

.circle .show {
  visibility: visible;
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
}

.check .show {
  visibility: visible;
  -webkit-animation: fadeIn 1s;
  animation: fadeIn 1s;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
