@font-face {
  font-family: fonteTitulo;
  src: url(../font/SuisseIntlCond-Medium-WebS.woff);
}

@font-face {
  font-family: fonteLabel;
  src: url(../font/SuisseIntlCond-Semibold-WebS.woff);
}
@font-face {
  font-family: fontenormal;
  src: url(../font/SuisseIntlCond-Thin-WebS.woff);
}

.SelectDepartments label {
  position: relative;
  font-family: fonteLabel;
  font-weight: 550;
  font-size: 2.1vh;
  display: flex;
  outline: none;
  color: #3E3C39;

  left: 16%;
  height: 3.5vh;
  width: 70%;
}

.SelectDepartments select {
  position: relative;
  width: 72%;
  height: 4vh;
  left: 12.5%;
  padding-left: 4vw;
  font-size: 2vh;
  letter-spacing: 0.01em;
  background: #FFFFFF;
  outline: none;

  box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.08);
  border-radius: 30px;
  border: #FFFFFF;
  color: #AEADAA;
}

.SelectDepartments select option {
  width: 30vh;
  height: 4vh;
  font-size: 12px;
  line-height: 16px;
  align-items: center;
  display: flex;
  letter-spacing: 0.01em;
  background: #ffffff;

  box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.08);
  border-radius: 30px;
  border: #ffffff;

  color: #3e3c39;
}
