body,
html {
  margin: 0;
  padding: 0;
}

#bg {
  position: relative;
  top: 0;
  margin-top: 0;
  width: 100%;
}

.Hamburguer {
  position: relative;
  left: 6.5%;
  top: 20%;
  z-index: 2;
}
.AppLogo {
  position: relative;
  left: 18%;
  top: 30%;
  height: 50%;
  width: 50%;
}
.normalTopBarCOLOR {
  background-color: #ffffff;
  box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.08);
}
.sidebarTopBarCOLOR {
  background-color: #fcc17a;
  box-shadow: 1px 4px 15px rgba(0, 0, 0, 0.08);
}

.logoMain {
  display: none;
}

@media (min-width: 961px){
  .Hamburguer {
    display: none;
  }

  .AppLogo {
    display: none;
  }

  .logoMain {
    position: relative;
    display: block;
    height: 12vh;
    width: 100%;
    z-index: 1;
    top: 4vh;
  }

  /*
  .App-logo-mainmenu {
    visibility: visible;
  }*/

}